import './MyContent.css'

import { Divider } from 'antd'


import { Row, Icon } from 'antd'
import GraduationIcon from '../static/graduation-hat.svg'


const masters = {
    position: "Master's Degree in Electromechanical Engineering",
    date: 'February 2019',
    organization: 'ETS Ingeniería y Diseño Industrial, Universidad Politécnica de Madrid',
    location: 'Madrid, Spain',
    bullets: [
        "Master's Thesis - Autonomous Agricultural Robot",
        'Programming Arduinos (I<sup>2</sup>C, MQTT, control PID) and Raspberry Pis (Flask, MongoDB)',
        'Programming Siemens PLC with STEP7 y WinCC',
        'Programming with NI LabView',  
        'Programming robot kinematics with MATLAB',
        'Simulation of electrical (QUCS) and mechanical (Festo FluidSIM, SAM) systems'
    ]
}

const Experience = ({ experience }) => (
    <>
        <Row type="flex" justify="space-between" align='middle' className='butt'>
            <div className='position'>{experience.position}</div>
            <div>{experience.date}</div>                
        </Row>
        <Row>
            {experience.organization}, <span className='location'>{experience.location}</span>
        </Row>
        {/* <ul className='bullets'>
            <li>Master's Thesis - Autonomous Agricultural Robot</li>
            <li>Programming Arduinos (I<sup>2</sup>C, MQTT, control PID) and Raspberry Pis (Flask, MongoDB)</li>
            <li>Programming Siemens PLC with STEP7 y WinCC</li>
            <li>Programming with NI LabView</li>
            <li>Programming robot kinematics with MATLAB</li>
            <li>Simulation of electrical (QUCS) and mechanical (Festo FluidSIM, SAM) systems</li>
        </ul> */}
        <ul>
            {experience.bullets.map((bullet) =><li>{bullet}</li>)}
        </ul>
    </>
)

export default () => (
    <div className='content-container'>
        <div className='entry'>
            <Divider><Icon type="user" className='divider-icon' />Bio</Divider>
            <div>
                <p>Graduate in MS Electromechanical Engineering at UPM (Spain) with a diverse background.</p>
                <p>Interested in programming, robotics, automation (especially RPA), solar energy, and the environment.</p>
                <p>Continuous learner.</p>
            </div>
            <div className='cv-container'>
                Feel free to download and distribute my CV
            </div>
        </div>
        <div className='entry'>
            <Divider><Icon type="trophy" className='divider-icon' />Hightlights</Divider>
            <p>Take a look at some of my projects, achievements, and experiences throughout the years</p>
            <div className='preview-container'>
                <img className='preview-image' src='../static/images/robot/robot0.jpeg' />
                <img className='preview-image' src='../static/images/solar/coolAndCasual.jpg' />
                <img className='preview-image' src='../static/images/rpa/diploma0.jpg' />
                <img className='preview-image' src='../static/images/bollos/wincc.jpg' />
                <img className='preview-image' src='../static/images/dobot/dobot0.jpg' />
            </div>
        </div>
        <div className='entry'>
            <Divider><GraduationIcon className='divider-icon-2' />Academics</Divider>
            <Experience experience={masters}/>
            {/* <Row type="flex" justify="space-between" align='middle' className='butt'>
                <div className='position'>Master's Degree in Electromechanical Engineering</div>
                <div>February 2019</div>                
            </Row>
            <Row>
                ETS Ingeniería y Diseño Industrial, Universidad Politécnica de Madrid, <span className='location'>Madrid, Spain</span>
            </Row>
            <ul className='bullets'>
                <li>Master's Thesis - Autonomous Agricultural Robot</li>
                <li>Programming Arduinos (I<sup>2</sup>C, MQTT, control PID) and Raspberry Pis (Flask, MongoDB)</li>
                <li>Programming Siemens PLC with STEP7 y WinCC</li>
                <li>Programming with NI LabView</li>
                <li>Programming robot kinematics with MATLAB</li>
                <li>Simulation of electrical (QUCS) and mechanical (Festo FluidSIM, SAM) systems</li>
            </ul> */}
        </div>
    </div>
)